.entry__input {
  background: none;
  border: none;
  border-bottom: 2px solid #ccc;
  width: 100%;
  height: 27px;
  margin: 0 0 30px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #fff;
}

.entry__input:focus {
  outline: none;
  border-bottom: 3px solid #ccc;
}