  .page {
    max-width: 1280px;
  }
}

@-webkit-keyframes dots {
  0%, 20% {
    color: rgba(0,0,0,0);
    text-shadow:
      5px 0 0 rgba(0,0,0,0),
      10px 0 0 rgba(0,0,0,0);}
  40% {
    color: white;
    text-shadow:
      5px 0 0 rgba(0,0,0,0),
      10px 0 0 rgba(0,0,0,0);}
  60% {
    text-shadow:
      5px 0 0 white,
      10px 0 0 rgba(0,0,0,0);}
  80%, 100% {
    text-shadow:
      5px 0 0 white,
      10px 0 0 white;}
}

@keyframes dots {
  0%, 20% {
    color: rgba(0,0,0,0);
    text-shadow:
      5px 0 0 rgba(0,0,0,0),
      10px 0 0 rgba(0,0,0,0);}
  40% {
    color: white;
    text-shadow:
      5px 0 0 rgba(0,0,0,0),
      10px 0 0 rgba(0,0,0,0);}
  60% {
    text-shadow:
      5px 0 0 white,
      10px 0 0 rgba(0,0,0,0);}
  80%, 100% {
    text-shadow:
      5px 0 0 white,
      10px 0 0 white;}
}

@-webkit-keyframes blackdots {
  0%, 20% {
    color: #fff;
    text-shadow:
      5px 0 0 #fff,
      10px 0 0 #fff;
    }
  40% {
    color: #000;
    text-shadow:
      5px 0 0 #fff,
      10px 0 0 #fff;
    }
  60% {
    text-shadow:
      5px 0 0 #000,
      10px 0 0 #fff;
    }
  80%, 100% {
    text-shadow:
      5px 0 0 #000,
      10px 0 0 #000;
    }
}

@keyframes blackdots {
  0%, 20% {
    color: #fff;
    text-shadow:
      5px 0 0 #fff,
      10px 0 0 #fff;
    }
  40% {
    color: #000;
    text-shadow:
      5px 0 0 #fff,
      10px 0 0 #fff;
    }
  60% {
    text-shadow:
      5px 0 0 #000,
      10px 0 0 #fff;
    }
  80%, 100% {
    text-shadow:
      5px 0 0 #000,
      10px 0 0 #000;
    }
}

.header {
  margin: 45px auto;
  border-bottom-color: rgba(84, 84, 84, 0.7);
  border-bottom-style: solid;
  border-bottom-width: 1px;
  max-width: calc(100% - 2*200px);
  display: flex;
  position: relative;
}

@media screen and (max-width:1280px) {
  .header {
    --horizontal-margin: calc(19px + 181*(100% - 768px)/(1280 - 768));
    max-width: calc(100% - 2*var(--horizontal-margin));
  }
}

@media screen and (max-width:768px) {
  .header {
    margin-top: 28px;
    max-width: 100%;
  }
}
.header__logo {
  margin: 0;
  width: auto;
  margin-bottom: 41px;
}

@media screen and (max-width: 768px) {
  .header__logo {
    margin-top: auto;
    margin-bottom:32px;
    margin-left: 27px;
    width: 104px;
    height: 24px;
  }
}
.header__link {
  margin: 6px 0 0 auto;
  text-decoration: none;
  color: #fff;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
}

@media screen and (max-width: 768px) {
  .header__link {
    margin-right: 27px;
  }
}
.header__email {

  margin: 6px 24px 0 auto;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #fff;
  text-decoration: underline;
}



@media screen and (max-width: 768px) {
  .header__email {
    position: absolute;
    bottom: 160px;
    left: 40%;
    text-align: center;
  }
}
.header__logout {
  margin: 6px 0 0;
  color: #a9a9a9;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-decoration: none;
}

@media screen and (max-width: 768px) {
  .header__logout {
    position: absolute;
    bottom: 120px;
    left: 45%;
    text-align: center;
  }
}
.header__humburger {
  display: flex;
  flex-direction: row;
  margin-left: auto;
  cursor: pointer;
  background-image: none;
}

.header__humburger:hover {
  opacity: .4;
}

@media screen and (max-width: 768px) {
  .header__humburger {
    width: 25px;
    height: 25px;
