.photo-grid__remove-button_visible {
  visibility: visible;
}
.photo-grid__likewrapper {
  display: flex;
  flex-direction: column;
}
.photo-grid__likes {
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  margin: 3px 0 0 18px;
  text-align: center;
}
.pop-up {
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 0.5s, opacity 0.5s linear;
  background-color: rgba(0,0,0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}


.pop-up_active {
  display: flex;
  visibility: visible;
  opacity: 1;
  transition-delay: 0s;
}
.pop-up__container {
  background-color: #fff;
  color: #000;
  width: 430px;
  box-sizing: border-box;
  border-radius: 10px;
  display: flex;
